.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
}
@supports (block-size: 100vh) and (padding-inline: 0) {
  .container {
    padding-inline: 1rem;
    block-size: 100vh;
  }
}
@supports not ((block-size: 100vh) and (padding-inline: 0)) {
  .container {
    padding: 0 1rem;
    height: 100vh;
  }
}
.container--main {
  justify-content: flex-end;
}
.container__text--wrapper {
  align-self: flex-start;
}
.container__text--wrapper h1,
.container__text--wrapper h5 {
  display: flex;
  flex-direction: column;
}
.container__text--wrapper h1 {
  color: var(--heading-clr);
  font-family: var(--font-ubuntu);
  font-size: 2rem;
}
.container__text--wrapper h5 {
  color: var(--text-clr);
  margin-block-start: 0.75rem;
  font-size: 0.9rem;
  font-weight: var(--font-wt-400);
  line-height: var(--body-lh);
  word-spacing: 1px;
}
.container a {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
@supports (margin-block-start: 0) {
  .container a {
    margin-block-start: 0.5rem;
  }
}
@supports not (margin-block-start: 0) {
  .container a {
    margin-top: 0.5rem;
  }
}
.container__image--wrapper img {
  display: block;
}
@supports (inline-size: 0) {
  .container__image--wrapper img {
    inline-size: 25rem;
  }
}
@supports not (inline-size: 0) {
  .container__image--wrapper img {
    width: 25rem;
  }
}